<template>
  <!--我的-->
  <div id="me" class="left">
    <home-header :title="'我的'" :back="true" :hideHead="true"></home-header>
    <div class="userInfo">
      <div class="userImg">
        <img src="../../assets/img/logo_new.png" alt="">
      </div>
      <p class="ners" @click="Login()">{{userName}}</p>
    </div>
    <div class="order">
      <div class="orderTop" @click="asOrder('全部订单')">
        <span>我的订单</span>
        <span>查看所有订单<i class="iconfont icon-arrow-right-copy"></i></span>
      </div>
      <ul class="orderType">
        <li @click="asOrder('待付款')">
          <i class="iconfont icon-daifukuan"></i>
          <span>待付款</span>
          <b>{{forPaymentNum}}</b>
        </li>
        <li @click="asOrder('待发货')">
          <i class="iconfont icon-medd_icon_dfh"></i>
          <span>待发货</span>
          <b>{{toSendNum}}</b>
        </li>
        <li @click="asOrder('待收货')">
          <i class="iconfont icon-peisongzhong"></i>
          <span>待收货</span>
          <b>{{harvestedNum}}</b>
        </li>
        <li @click="asOrder('退货退款')">
          <i class="iconfont icon-ziyuan"></i>
          <span>退货退款</span>
          <b>{{refundsNum}}</b>
        </li>
      </ul>
    </div>
    <ul class="personalList">
      <li @click="asLogin('resetPwd')"><span>修改密码</span><i class="iconfont icon-arrow-right"></i></li>
      <li @click="asLogin('addressList')"><span>管理收货地址</span><i class="iconfont icon-arrow-right"></i></li>
      <li @click="$router.push({path:'/problem'})"><span>常见问题</span><i class="iconfont icon-arrow-right"></i></li>
      <p><span>在线客服</span><i class="iconfont icon-arrow-right"></i></p>
    </ul>
    <div class="mine-logout" @click="logout()" v-if="logoutShow">
      退出当前账号
    </div>
  </div>
</template>

<script>
  import {Toast} from 'mint-ui'
  import homeHeader from '../../components/header/header.vue'

  export default {
    data() {
      return {
        logoutShow: false,
        userName: '登录/注册',
        forPaymentNum: 0,   //待付款订单数量
        toSendNum: 0,       //待发货订单数量
        harvestedNum: 0,    //待收获订单数量
        refundsNum: 0,      // 退货退款订单数量
      }
    },
    computed: {},
    components: {
      homeHeader,
    },
    methods: {
      //验证是否登录
      isLogin() {
        var memberId = window.localStorage.getItem('memberId');
        var username = window.localStorage.getItem('username');
        if (memberId) {
          this.userName = username;
          this.logoutShow = true;
        } else {
          this.userName = '登录/注册';
          this.logoutShow = false;
        }
      },
      getStatusNum(){
        let that=this;
        if(that.logoutShow){
          var memberId = window.localStorage.getItem('memberId');
          that.ajax({
            url:'/order/getStatusList?memberId='+memberId,
            success:function (res) {
              if(res.status==1000){
                var info=res.data;
                console.log(info[0].status)
                if(info[0].status==10){
                  that.forPaymentNum=info[0].number
                }
                if(info[1].status==30){
                  that.toSendNum=info[1].number
                }
                if(info[2].status==60){
                  that.harvestedNum=info[2].number
                }
              }
            }
          })
        }
      },
      //未登录时点击登录跳转到登录页面。
      Login() {
        var memberId = window.localStorage.getItem('memberId');
        var username = window.localStorage.getItem('username');
        if (!memberId) {
          this.$router.push({path: '/login'})
        }
      },
      asOrder(val) {
        var memberId = window.localStorage.getItem('memberId');
        if (memberId) {
          this.$router.push({path: '/myOrder', query: {title: val}})
        } else {
          Toast('您还未登录，请先登录')
        }
      },
      asLogin(val) {
        var memberId = window.localStorage.getItem('memberId');
        if (memberId) {
          this.$router.push({path: '/' + val})
        } else {
          Toast("您还未登录，请先登录")
        }
      },
      logout() {
        let that = this;
        window.localStorage.removeItem('username');
        window.localStorage.removeItem('memberId');
        that.logoutShow = false;
        that.userName = '登录/注册'
        that.ajax({
          url: '/member/sign/out',
          success: function (res) {
            if (res.status == 1000) {
              window.localStorage.removeItem('MemberId');
              window.sessionStorage.removeItem('tempMemberId');
            }
          }
        })
      },
      save() {
        this.logoutShow = localStorage.getItem('hlxc_userInfo');
      }
    },
    created() {
      this.save();
      this.isLogin();
      this.getStatusNum();
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  #me {
    float: left;
    width: 100%;
  }

  .userInfo {
    background: $red;
    padding: 10%;
    text-align: center;
    .userImg {
      height: 20%;
      margin-bottom: 5%;
      img {
        width: 20%;
        display: block;
        margin: 0 auto;
      }
    }
    .ners {
      color: $f;
    }
  }

  .myList {
    background: $f;
    padding: 2% 2%;
    border-bottom: 1px solid #F4F4F4;
    display: flex;
    justify-content: space-between;
    display: -webkit-flex;
    margin-bottom: 1rem;
    li {
      text-align: center;
      width: 20%;
      display: flex;
      display: -webkit-flex;
      flex-direction: column;
      span {
        font-size: 0.9rem;
      }
    }
  }

  .order {
    border-top: 1px solid #F4F4F4;
    border-bottom: 1px solid #F4F4F4;
    margin-bottom: 5%;
    .orderTop {
      background: $f;
      border-bottom: 1px solid #F4F4F4;
      display: flex;
      display: -webkit-flex;
      padding: 3% 5%;
      justify-content: space-between;
      .iconfont {
        font-size: $med;
      }
    }
    .orderType {
      background: $f;
      display: flex;
      display: -webkit-flex;
      padding: 1rem 15% 0 5%;
      justify-content: space-between;
      li {
        position: relative;
        width: 20%;
        display: flex;
        display: -webkit-flex;
        text-align: center;
        font-size: 0.9rem;
        flex-direction: column;
        padding: 5% 0;
        i.iconfont {
          font: 2.5rem/2.5rem "Microsoft YaHei";
          margin-bottom: 0.8rem;
        }
        b {
          position: absolute;
          background: $red;
          box-sizing: border-box;
          border-radius: .9rem;
          left: 50%;
          margin-left: .2rem;
          top: 0.4rem;
          color: $f;
          font-size: 1.2rem;
          min-width: 1.6rem;
          padding: 0 .4rem;
          height: 1.6rem;
          line-height: 1.6rem;
        }
      }
    }
  }

  .personalList {
    border-bottom: 1px solid #F4F4F4;
    border-top: 1px solid #F4F4F4;
    padding: 0 6% 0 4%;
    background: $f;
    margin-bottom: 1rem;
    li {
      display: flex;
      font: 1rem/3rem "Microsoft YaHei";
      display: -webkit-flex;
      justify-content: space-between;
      border-bottom: 1px dashed #F4F4F4;
      span {

      }
      i.iconfont {
        margin-top: 0.3rem;
        font: 2.5rem/2.5rem "Microsoft YaHei";
      }
    }
    p {
      display: flex;
      display: -webkit-flex;
      justify-content: space-between;
      font: 1rem/3rem "Microsoft YaHei";
      i.iconfont {
        margin-top: 0.3rem;
        font: 2.5rem/2.5rem "Microsoft YaHei";
      }
    }
  }

  .mine-logout {
    width: 60%;
    text-align: center;
    background: $red;
    color: $f;
    margin: 0 auto $bottom;
    @include sh($med, 2.6em);
    border-radius: 3px;
  }
</style>



